.about {
  max-width: 880px;
  margin: 0 auto;
}

.about__title {
  margin: 50px auto;
  text-align: center;
  font-size: 40px;
}

.about__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  margin-bottom: 20px;
}
.about__feature-item {
  display: flex;
  align-items: center;
  gap: 10px;
}
.about__description-text,
.about__features {
  grid-column: 1;
}
.about__description {
  display: flex;
  flex-direction: column;
  font-size: 16px;
}
.about__image {
  grid-column: 2;
  width: 100%;
  align-self: center;
}

.about__features {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding-left: 10px;
  row-gap: 8px;
}

.about__icono {
  width: 30px;
}

@media (max-width: 950px) {
  .about__wrap {
    margin: 0 auto;
    width: 88%;
  }
}

@media (max-width: 600px) {
  .about__title {
    width: 90%;
    margin: 20px auto;
    font-size: 38px;
  }
  .about__wrap {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr auto;
    margin-bottom: 20px;
  }
  .about__description-text,
  .about__features {
    grid-row: 1;
    grid-column: 1;
  }
  .about__image {
    grid-row: 2;
    grid-column: 1;
    width: 100%;
  }
}
@media (max-width: 400px) {
  .about__description-text {
    font-size: 14px;
  }
  .about__feature-text {
    font-size: 14px;
  }
}
