.navigation {
  background-color: transparent;
  position: relative;
}

.navigation__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  gap: 14px;
}

.navigation__item {
  flex-grow: 1;
  display: flex;
  height: auto;
}

.navigation__link {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 5px 10px;
  color: white;
  position: relative;
  transition: background-color 0.3s ease;
}

.navigation__link:hover {
  background-color: rgba(4, 136, 159, 0.221);
  border-radius: 5px;
  border-top: 2px solid blue;
}

.navigation__list--active {
  border-bottom: 2px solid blue;
}

.hamburger {
  display: none;
  background-color: transparent;
  border: none;
  font-size: 24px;
  color: white;
  cursor: pointer;
}

@media (max-width: 600px) {
  .navigation__list {
    flex-direction: column;
    position: absolute;
    top: 50px;
    right: 1px;
    width: 130px;
    background-color: rgb(0, 8, 23);
    display: none;
    font-size: 14px;
    z-index: 5;
    text-align: right;
    justify-content: center;
    border-radius: 8px;
  }
  .navigation__list.open {
    display: flex;
  }

  .hamburger {
    display: block;
  }
}

@media (max-width: 431px) {
  .navigation__list {
    right: 20px;
  }
  .hamburger {
    margin-right: 10px;
  }
}
