.footer {
  max-width: 880px;
  display: flex;
  margin: auto;
  color: white;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 24px;
}
.footer__social-media {
  display: flex;
  justify-content: space-between;
  width: 100px;
  margin: auto;
}
.footer__logos {
  margin: 10px auto 15px;
  height: 30px;
  opacity: 0.4;
  transition: opacity 0.7s;
}
.footer__logos:hover {
  opacity: 1;
  cursor: pointer;
}

.footer__copyright {
  margin: auto;
  max-width: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22;
  color: #545454;
}

@media (max-width: 887px) {
  .footer {
    max-width: 90%;
  }
  .footer__copyright {
    height: 15px;
  }
}

@media (max-width: 887px) {
  .footer__copyright {
    margin: 10px auto 0;
    padding-bottom: 20px;
    font-size: 14px;
    line-height: 1.21;
  }
}

@media (max-width: 400px) {
  .footer {
    max-width: 100%;
  }
}
