.musicSearch {
  max-width: 880px;
  margin: 0 auto;
  min-height: 484px;
}

.musicSearch__title {
  margin: 24px auto;
  text-align: center;
  font-size: 44px;
}
.musicSearch__form {
  max-width: 400px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.musicSearch__input {
  height: 28px;
  border-radius: 12px;
  border: none;
  outline: none;
  text-align: center;
}
.music-search__button {
  margin-left: 16px;
  font-size: 16px;
  width: 80px;
  height: 32px;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  color: white;
  background-image: linear-gradient(to right, #9915e5, #9e112b);
  opacity: 1;
  transition: opacity 0.7s;
  font-weight: 500;
}
.music-search__button:disabled {
  background-image: linear-gradient(to right, #ccc, #ccc);
  cursor: not-allowed;
  opacity: 0.5;
  pointer-events: none;
}
.music-search__button:hover {
  opacity: 0.7;
}
.musicSearch__grid {
  max-width: 880px;
  margin: 50px auto 10px;
  display: grid;
  grid-template-columns: repeat(3, 282px);
  column-gap: 17px;
  row-gap: 20px;
  min-height: 318px;
}
.musicSearch__gridItem {
  max-width: 282px;
  border-radius: 10px;
  background-color: #ffffff;
  padding-bottom: 31px;
  position: relative;
}

.musicSearch__gridImage {
  width: 100%;
  height: 282px;
  object-fit: cover;
  max-height: 282px;
  border-radius: 10px 10px 0px 0px;
  cursor: pointer;
}
.musicSearch__gridDescription {
  width: 246px;
  height: 45px;
  margin: 14px auto 0;
  color: #000000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.musicSearch__gridArtist {
  width: 225px;
  margin: 0 auto;
  padding: 0;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.2;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.musicSearch__gridAlbum {
  width: 225px;
  margin: 0 auto;
  padding: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 1.2;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
@media (max-width: 1024px) {
  .musicSearch__grid {
    width: 90%;
  }
}
@media (max-width: 887px) {
  .musicSearch__grid {
    grid-template-columns: repeat(2, 282px);
    grid-template-rows: auto;
    justify-content: center;
  }
  .musicSearch__title {
    width: 90%;
  }
}
@media (max-width: 640px) {
  .musicSearch__grid {
    grid-template-columns: repeat(1, 282px);
    grid-template-rows: auto;
    justify-content: center;
    margin: 36px auto 0;
  }
  .musicSearch__title {
    font-size: 38px;
  }
}
@media (max-width: 440px) {
  .musicSearch__grid {
    min-height: 250px;
  }
  .musicSearch__input {
    width: 150px;
  }
}
