.spotifyPopup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.spotifyPopup__content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 370px;
  cursor: default;
}
.spotifyPopup__title {
  color: black;
  font-size: 32px;
  margin: 0 auto;
  text-align: center;
}
.spotifyPopup__buttonContainer {
  display: flex;
  max-width: 250px;
  margin: 24px auto 0;
}

.spotifyPopup__button {
  width: 80px;
  height: 40px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  margin: 0 auto;
  color: white;
  transition: transform 0.2s, background-color 0.2s;
}

.spotifyPopup__button--confirm {
  background-color: rgb(48, 133, 214);
}
.spotifyPopup__button--close {
  background-color: rgb(221, 51, 51);
}
.spotifyPopup__button:hover {
  transform: scale(1.05);
}

.spotifyPopup__button--confirm:hover {
  background-color: rgb(28, 113, 194);
}

.spotifyPopup__button--close:hover {
  background-color: rgb(191, 41, 41);
}

@media (max-width: 555px) {
  .spotifyPopup__content {
    max-width: 200px;
  }
  .spotifyPopup__title {
    font-size: 28px;
  }
}
